.Endscreen{
	background-color: rgba(0, 0, 0, 0.5);
	position: fixed;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	display: flex;
	flex-flow: column;
	align-items: center;
}

.Button{
	border:1px solid black;
	background-color: white;
	color: black;
	border-radius: 5px;
	padding: 5px 10px;
	width: 10%;
	margin: 20px;
}

